import { openBlankLink } from '@/utils/helpers';

const urlMap = {
  Discord: 'https://discord.com/invite/GmJCZmpFeH',
  Notion: 'https://buidlerdao.notion.site',
  Twitter: 'https://twitter.com/TheBuilderDAO',
  YouTube: 'https://www.youtube.com/channel/UCO9TRqgc6Q-MMGy72_eGPNA',
  Vitae3: 'https://discord.com/invite/GmJCZmpFeH'
};
const urlKeys = Object.keys(urlMap);
const handleView = (key: string) => {
  const link = urlMap[key];
  openBlankLink(link);
};
const Contrats = (): JSX.Element => {
  return (
    <div className="font-si text-darkGreen font-normal flex gap-[24px] cursor-pointer">
      {urlKeys.map((key) => (
        <span onClick={() => handleView(key)} key={key}>
          {key}
        </span>
      ))}
    </div>
  );
};

export default Contrats;
