/**
 * 格式化 url 参数
 * @param baseUrl
 * @param jsonData
 * @returns string
 */
export const buildUrlWithJsonData = (
  baseUrl: string,
  jsonData: Record<string, any>
): string => {
  const searchParams = new URLSearchParams(jsonData);
  const fullUrl = `${baseUrl}?${searchParams.toString()}`;
  return fullUrl;
};

export const address2Avater = async (address = '') => {
  const avatar = jazzicon(64, parseInt(address.slice(2, 8), 16));
  document.body.appendChild(avatar);
  const dataUrl = await htmlToImage.toPng(avatar as any);
  document.body.removeChild(avatar);
  return dataUrl;
};
