import { useEffect, useRef, useState } from 'react';
import { useAppSetting } from '@/store/appSetting';
import { ethers } from 'ethers';
import brpAbi from '@/abi/BRP.json';
import pbrpAbi from '@/abi/pBRP.json';
import { formatUnits } from '@ethersproject/units';

const Profile = (): JSX.Element => {
  const { auth } = useAppSetting();

  const [pBRP, setPBRP] = useState('0');
  const [BRP, setBRP] = useState('0');

  const ref = useRef(false);
  useEffect(() => {
    if (ref.current) {
      ref.current = true;
    } else if (auth.address) {
      console.log(auth, 'auth 不是第一次打开');
      getBRPBalance();
      getPBRPBalance();
    }
  }, [auth]);

  const getBRPBalance = () => {
    // const provider = new ethers.providers.Web3Provider(window.ethereum);
    // 选择 Mumbai Polygon 网络
    const network = import.meta.env.VITE_MUMBAI_NETWORK;
    const provider = new ethers.providers.JsonRpcProvider(network);
    const contractAddress = import.meta.env.VITE_BRP_CONTRACT_ADDRESS;
    // const contractAddress = '0xab781188d4f41f36efff50778dc873f68f97e939';
    const brpContract = new ethers.Contract(contractAddress, brpAbi, provider);

    brpContract.balanceOf(auth.address).then((value: any) => {
      const brp = formatUnits(value.toString(), 4);
      setBRP(brp);
    });
  };

  const getPBRPBalance = () => {
    // const provider = ethers.getDefaultProvider();
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const contractAddress = import.meta.env.VITE_PBRP_CONTRACT_ADDRESS;
    const pbrpContract = new ethers.Contract(
      contractAddress,
      pbrpAbi,
      provider
    );
    pbrpContract.balanceOf(auth.address).then((value: any) => {
      const pbrp = formatUnits(value.toString(), 18);
      setPBRP(pbrp);
    });
  };
  return (
    <div className="bg-black text-white font-bold">
      <div className="text-title flex gap-[46px] mr-[80px]">
        <span>{pBRP} pBRP</span>
        <span>{BRP} BRP</span>
      </div>
    </div>
  );
};

export default Profile;
