import { useAppSetting } from '@/store/appSetting';
import type { MenuProps } from 'antd';
import { Dropdown } from 'antd';
import Typography from '../typography';
import { checkoutToken, loginOut, utilLogin } from '@/utils/checkLogin';
import { useEffect, useRef, useState } from 'react';
import toast from '../toast';
import { openBlankLink } from '@/utils/helpers';
import { DownOutlined } from '@ant-design/icons';
import SvgIcon from '@/components/svgIcon';
import './index.css';

const Connect = (): JSX.Element => {
  const { auth, setAuth, setClaim, claim } = useAppSetting();
  const ref = useRef(false);

  const [clickable, setClickable] = useState(true);
  const handleLink = async () => {
    setClickable(false);
    const isLogin = checkoutToken();
    if (!isLogin) {
      openBlankLink(
        `https://buidlerdao.gitbook.io/buidlerdao-whitepaper-en/contributions-and-benefits/types-of-benefits/a.-reputation-token-usdbrp`
      );
    } else {
      setClickable(true);
    }
  };

  // login out
  const handleLogout = () => {
    setAuth({
      address: '',
      // name: '',
      avatar: ''
    });
    // setClaim({
    //   ...claim,
    //   toClaim: 0
    // });
    loginOut();
  };

  // login
  const handleLogin = async () => {
    setClickable(false);
    try {
      const res: any = await utilLogin();
      if (res) {
        setAuth(res.auth);
      }
      setClickable(true);
    } catch (error) {
      setClickable(true);
    }
  };

  const handleAccountChange = (...args) => {
    const accounts = args[0];
    if (accounts.length === 0) {
      console.log('Please connect to metamask');
    } else if (accounts[0] !== auth.address) {
      toast.success('Account changed! Please verify account on metamask!');
      setTimeout(() => {
        handleLogin();
      }, 2500);
    }
  };

  useEffect(() => {
    window.ethereum?.on('accountsChanged', handleAccountChange);
    return () => {
      window.ethereum?.removeListener('accountsChanged', handleAccountChange);
    };
  });

  useEffect(() => {
    console.log('66666', auth);
    if (ref.current) {
      ref.current = true;
    } else {
      if (auth.address) {
        console.log(auth.address, 'auth.address');
      }
    }
  }, []);

  const items: MenuProps['items'] = [
    {
      label: (
        <Typography
          variant="largeBody"
          className="address-drop-item my-[18px] mx-[10px] cursor-pointer mb-[10px]"
          onClick={() => (clickable ? handleLink() : null)}
        >
          How it works
        </Typography>
      ),
      key: '0'
    },
    {
      label: (
        <Typography
          variant="largeBody"
          className="address-drop-item my-[18px] mx-[10px] cursor-pointer flex justify-between"
          onClick={handleLogout}
        >
          <p>Disconnect</p>
          <SvgIcon name={'logout'} size={17} />
        </Typography>
      ),
      key: '1'
    }
  ];

  return auth.address ? (
    <Dropdown menu={{ items }}>
      <div className="flex h-[50px] w-[200px] cursor-pointer items-center justify-center rounded-[12px] border border-[#fff] px-[4px]">
        <img
          alt=""
          src={auth.avatar}
          className="mr-[6px] h-[24px] w-[24px] rounded-[25px] object-cover"
        />
        <Typography
          variant="subTitle"
          className="text-lg font-[700] text-white"
        >
          {/* {auth.address} */}
          {auth.address && auth.address.replace(/(\w{5})\w+(\w{4})/, '$1...$2')}
        </Typography>
        <DownOutlined
          rev={undefined}
          className="ml-[10px] mt-[8px] w-[24px] h-[24px] text-white font"
        />
      </div>
    </Dropdown>
  ) : (
    <div
      onClick={() => (clickable ? handleLogin() : null)}
      className="rounded-lg flex h-[50px] w-[200px] cursor-pointer items-center justify-center border-[1px] border-[#fff] px-[12px]"
    >
      <Typography variant="subTitle" className="text-lg font-[700] text-white">
        Connect Wallet
      </Typography>
    </div>
  );
};
export default Connect;
